* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-family: "Saira Condensed";
  --nude: #e7cdbf;
}

.header {
  height: 84px;
  background: #121212;
  display: flex;
  justify-content: center;
  align-items: center;

  .navigation-bar {
    width: 1120px;
    height: 64px;
    display: flex;
    justify-content: space-between;
    top: 10px !important;

    .navigation-bar-left-content {
      display: inline-flex;
      align-items: center;
      gap: 11px;

      .logo {
        width: 64px;
        height: 64px;
      }

      .project-name {
        font-size: 32px;
        font-weight: 700;
        color: #fff;
      }
    }

    .navigation-bar-right-content {
      display: flex;
      width: 600px;
      justify-content: space-between;
      align-items: center;
      flex-shrink: 0;

      a {
        color: #fff;
        font-size: 20px;
        font-weight: 700;
        padding: 8px 15px;
        cursor: pointer;
        padding: 7px 21px;

        &:hover {
          opacity: 0.7;
        }
      }

      .staff {
        border-radius: 24px;
        background: #df0012;

        &:hover {
          opacity: 0.7;
        }
      }
    }
  }
}

